
.card-container {
    width: 300px;
    overflow: hidden;
    box-shadow:  0px 0px 15px -5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: 60px;
    margin-top: 100px;
    transition: 0.3s;
    animation: ease-in;
    font-size: 12px;

}

.card-container:hover{
    transform: scale(1.1);
    box-shadow:  0px 0px 15px 0px;
}

.card-content {
    width: 250px;
    text-align: justify;
}

.image-container img {
    width: 300px;
    height: 230px;
}

.btn {
    margin-bottom: 20px;
    display: flex;
    justify-content: center;
}

.btn button {
    padding: 0.4rem;
    transition: 0.2s;
}